import { useReducer } from "react";
import { ThemeProvider } from "styled-components";
import React from 'react'
import Main from "./Main";
import { createGlobalStyle } from 'styled-components'

const GlobalStyle = createGlobalStyle`
  body {
    background-color: ${props => props.theme.main_bg};
    margin: 0;
  };
  * {
  box-sizing: border-box;
  };
`;


const firstTheme = {
  main_bg: 'hsl(222, 26%, 31%)',
  toggle_keypad_bg: 'hsl(223, 31%, 20%)',
  screen_info_bg: 'hsl(224, 36%, 15%)',
  key_bg: 'hsl(30, 25%, 89%)',
  key_res_del_bg: 'hsl(225, 21%, 49%)',
  key_equal_bg: 'hsl(6, 63%, 50%)',
  key_bg_textcolor: 'hsl(221, 14%, 31%)',
  text1: 'hsl(0, 0%, 100%)',
  text2: 'hsl(221, 14%, 31%)',
  text3: 'hsl(0, 0%, 100%)',
};

const secondTheme = {
  main_bg: 'hsl(0, 0%, 90%)',
  toggle_keypad_bg: 'hsl(0, 5%, 81%)',
  screen_info_bg: 'hsl(0, 0%, 93%)',
  key_bg: 'hsl(45, 7%, 89%)',
  key_res_del_bg: 'hsl(185, 42%, 37%)',
  key_equal_bg: 'hsl(25, 98%, 40%)',
  key_bg_textcolor: 'hsl(60, 10%, 19%)',
  text1: 'hsl(60, 10%, 19%)',
  text2: 'hsl(60, 10%, 19%)',
  text3: 'hsl(0, 0%, 100%)',
};

const thirdTheme = {
  main_bg: 'hsl(268, 75%, 9%)',
  toggle_keypad_bg: 'hsl(268, 71%, 12%)',
  screen_info_bg: 'hsl(268, 71%, 12%)',
  key_bg: 'hsl(268, 47%, 21%)',
  key_res_del_bg: 'hsl(281, 89%, 26%)',
  key_equal_bg: 'hsl(176, 100%, 44%)',
  key_bg_textcolor: 'hsl(52, 100%, 62%)',
  text1: 'hsl(52, 100%, 62%)',
  text2: 'hsl(52, 100%, 62%)',
  text3: 'hsl(0, 0%, 100%)',
};

const themes = {
  first: firstTheme,
  second: secondTheme,
  third: thirdTheme,
}

const initialState = {theme: 'first'};

function reducer(state, action) {
  switch (action.type) {
    case 'theme1':
      return {theme: 'first'};
    case 'theme2':
      return {theme: 'second'};
    case 'theme3':
      return {theme: 'third'};
    default:
      throw new Error();
  }
}

function App() {
  const [state, dispatch] = useReducer(reducer, initialState);
  return (
    <ThemeProvider theme={themes[state.theme]}>
      <GlobalStyle />
      <Main theme={state.theme} dispatch={dispatch} />
    </ThemeProvider>
  );
}

export default App;